<!-- チャットの送受信コンポーネント-->
<!-- 送信ボタンを押すと、バックエンドにinputText(入力された会社名)が渡される-->
<template>
	<div class="chat-container">
		<div class="chat-out">
			<div v-for="message in gptMessages" :key="message.id" class="chat-message"> {{ message.gptText }} </div>
		</div>
		<div class="form-control">
			<input type="text" v-model="inputText" @keydown.enter="sendGPT" placeholder="企業の名前を入力" class="col-md-11">
			<button @click="sendGPT" class="btn btn-secondary col-md-1">送信</button>
		</div>
	</div>
</template>

<script setup>
import { ref } from 'vue';

const inputText = ref('');//ユーザーから入力される会社名
const gptMessages = ref([//GPTから帰ってきた、出力
	{id:1, gptText:'いちですそんあこんな\nだうえいふぁい'},
	{id:2, gptText:'にーです'},
	{id:3, gptText:'いちです'},
]);
//チャットGPTへの送受信
function sendGPT() {
	

	inputText.value = '';
}
</script>

<style scoped>
.chat-container{
	height: 100vh; 
	display: flex;
	flex-direction: column;
}
.chat-out{
	background-color: #f0f0f0;
	flex: 1;
	overflow-y: auto; 
	padding: 1em;
}
.chat-message{
	background-color: rgb(207, 207, 207);
	padding: 1em;
	margin-bottom: 0.5em;
	border-radius: 0.5em;
}
</style>